.resumeTitle {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, minmax(0, 1fr));

}

.resumeLi {
    width: 100%;
    height: 5rem;
    background: var(--black);
    background-color: rgb(39, 48, 73, 0.9);
    color: rgb(229 231 235);
    font-size: 1.25rem;
    line-height: 1.75rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0px;
    border: none;

}

.resumeLi:hover {
    background-color: rgb(39, 48, 73);
    transition-duration: 300ms;
    transition: 0.3s ease-in-out;
    cursor: pointer;
}

.education {
    padding-top: 2rem;
    padding-bottom: 2rem;

}

.education p {
    color: var(--yellow);
    font-size: 0.875rem;
    line-height: 1.25rem;
    letter-spacing: 4px;
}

.education h2 {
    color: var(--black);
    font-weight: bold;
    font-size: 2.25rem;
    line-height: 2.5rem;
}

@media screen and (max-width:768px) {
    .resumeTitle{
        grid-template-columns: repeat(1, minmax(0, 1fr));
    }
}
