.container{
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin-bottom: 3.5rem;
}
.title {
    font-size: 0.875rem;
    line-height: 1.25rem;
    text-transform: uppercase;
    font-weight: lighter;
    color: var(--yellow);
}

.description {
    font-size: 3rem;
    line-height: 1;
    text-transform: uppercase;
    font-weight: bold;
}