.banner{
    display: flex;
    align-items: center;
    justify-content: center;

}
.bannerLeft {
    width: 50%;
    display: flex;
    flex-direction: column;
    gap: 3rem;
}

.intro {
    display: flex;
    flex-direction: column;
    gap: 0.75rem;
}

.intro h4 {
    font-size: 1.5rem;
    line-height: 1.75rem;
    letter-spacing: 0.025em;
    color: var(--black);
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif
}

.intro h1 {
    font-size: 3rem;
    font-weight: bold;
    color: var(--black);
}

.intro h1 span {
    color: var(--yellow);
    text-transform: uppercase;
}

.intro h2 {
    font-size: 2.25rem;
    line-height: 2.5rem;
    color: var(--black);
    font-weight: bold;
}

.intro p {
    color: var(--black);
    line-height: 1.5rem;
    letter-spacing: 0.025em;
}

.icons {
    display: flex;
    gap: 2rem;
}

.bannerIcon {
    width: 3rem;
    height: 3rem;
    background: var(--black);
    --tw-bg-opacity: 0.25;
    color: rgb(229 231 235);
    font-size: 1.25rem;
    line-height: 1.2rem;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.375rem;
    box-shadow: 1px 1px 5px #454850, -1px -1px 5px #8e9192;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-duration: 300ms;
}

.bannerIcon:hover {
    --tw-bg-opacity: 0.4;
    transform: translateY(0.25rem);
    color: #a2d133;
    cursor: pointer;
}

.socials {
    display: flex;
    justify-content: space-between;
    gap: 3rem;
}

.bannerRight {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  
}
@media  screen and (max-width:768px) {
    .socials{
        flex-direction: column;
    }
    .bannerRight{
        display: none;
    }
}