*{
  padding: 0;
  margin: 0;
}
@font-face {
  font-family: Gumela;
  src: url(../public/fonts/Gumela.ttf);
}
*{
  font-family: 'Gumela';
  font-size: 400;
}
body{
  background: #eaffc9;
 
}

:root
{
  --yellow: #F5C32C;
    --orange : #FCA61F;
    --black : #242D49;
    --gray : #788097;
    --blueCard : #DDF8FE;
    --purple: rgb(238 210 255);
    --boxShadow : 0px 19px 60px rgb(0 0 0 / 8%);
    --orangeCard: rgba(252, 166, 31, 0.45);
    --smboxShadow: -79px 51px 60px rgba(0, 0, 0, 0.08);
}

.App {
  text-align: center;
}

.active{
  color: #a2d133;
}
.section{
  width: 100%;
  background: #eaffc9;
  border-bottom: 1px solid var(--black);
  padding-bottom: 20px;
  max-width: 75rem;
  margin: auto;
  padding:16px;

}




