.card {
    width: 100%;
    height: 20rem;
   
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-radius: 0.5rem;
    box-shadow: 8px 8px 19px #777a83, -8px -8px 19px #bfd194;
    display: flex;
    align-items: center;
    background-image: linear-gradient(rgb(230, 252, 196), #e3fdba);
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 100ms;

    --tw-gradient-from: currentColor var(--tw-gradient-from-position);
    --tw-gradient-to: rgb(255 255 255 / 0) var(--tw-gradient-to-position);
    --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to)
}


.card:hover {
    
    cursor: pointer;
 
    transition-property: transform;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    

}


.aa {
    height: 18rem;
    overflow-y: hidden;


}

.aa:hover {
    overflow-y: visible;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
}

.container2 span {
    font-size: 3rem;
    line-height: 1;
    color: var(--black);
}

.container2 {
    display: flex;
    flex-direction: column;
    transition: 0.3s ease-in-out;
    gap: 0.5rem;    
    height: 100%; 
    transform: translateY(4.75rem);
}

.container2:hover {
    transform: translateY(2rem);

    transition-property: transform;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
  
   
    
}

.wraper {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    color: var(--black);
}

.wraper p {
    color: var(--black);
}

.wraper h2 {
    font-size: 1.5rem;
    line-height: 2rem;
    font-weight: bold;
    color: var(--black)
}

@media screen and (max-width:768px) {
    .card{
      
      width: fit-content;
    
    }
    .container2 {
        transform: translateY(1rem);
    }
}